import React, { useEffect } from "react";
import "./Map.css";

declare global {
  // eslint-disable-next-line no-unused-vars
  interface Window {
    kakao: any;
  }
}

const { kakao}: Window  = window;

const Map = ({ latitude, longitude }: any) => {
  useEffect(() => {
    //        
    let placeOverlay = new kakao.maps.CustomOverlay({ zIndex: 1 }),
      contentNode = document.createElement("div"), //     
      markers: any = [], //   
      currCategory = ""; //      
    const mapContainer = document.getElementById("map"),
      mapOptions = {
        center: new kakao.maps.LatLng(latitude, longitude),
        level: 3,
      };
    //  
    const map = new kakao.maps.Map(mapContainer, mapOptions);
    //    
    const ps = new kakao.maps.services.Places(map);
    //  idle  
    kakao.maps.event.addListener(map, "idle", searchPlaces);
    //   
    const markerPosition = new kakao.maps.LatLng(latitude, longitude);
    //  
    const marker = new kakao.maps.Marker({
      position: markerPosition,
    });
    //     
    marker.setMap(map);
    //     css class 
    contentNode.className = "placeinfo_wrap";
    //     mousedown, touchstart  
    //        kakao.maps.event.preventMap  
    addEventHandle(contentNode, "mousedown", kakao.maps.event.preventMap);
    addEventHandle(contentNode, "touchstart", kakao.maps.event.preventMap);
    //    
    placeOverlay.setContent(contentNode);
    //     
    addCategoryClickEvent();

    //     
    function addEventHandle(target: any, type: any, callback: any) {
      if (target.addEventListener) {
        target.addEventListener(type, callback);
      } else {
        target.attachEvent("on" + type, callback);
      }
    }

    //    
    function searchPlaces() {
      if (!currCategory) {
        return;
      }
      //   
      placeOverlay.setMap(null);
      //     
      removeMarker();
      ps.categorySearch(currCategory, placesSearchCB, { useMapBounds: true });
    }

    //      
    function placesSearchCB(data: any, status: any) {
      if (status === kakao.maps.services.Status.OK) {
        //      
        displayPlaces(data);
      } else if (status === kakao.maps.services.Status.ZERO_RESULT) {
        //        
      } else if (status === kakao.maps.services.Status.ERROR) {
        //            
      }
    }

    //    
    function displayPlaces(places: any) {
      //     
      //       
      const order = document
        .getElementById(currCategory)
        ?.getAttribute("data-order");
      for (let i = 0; i < places.length; i++) {
        //    
        const marker = addMarker(
          new kakao.maps.LatLng(places[i].y, places[i].x),
          order
        );
        //      
        //     
        (function (marker, place) {
          kakao.maps.event.addListener(marker, "click", function () {
            displayPlaceInfo(place);
          });
        })(marker, places[i]);
      }
    }

    //       
    function addMarker(position: any, order: any) {
      const imageSrc =
          "https://t1.daumcdn.net/localimg/localimages/07/mapapidoc/places_category.png", //   url,   
        imageSize = new kakao.maps.Size(27, 28), //   
        imgOptions = {
          spriteSize: new kakao.maps.Size(72, 208), //   
          spriteOrigin: new kakao.maps.Point(46, order * 36), //       
          offset: new kakao.maps.Point(11, 28), //      
        },
        markerImage = new kakao.maps.MarkerImage(
          imageSrc,
          imageSize,
          imgOptions
        ),
        marker = new kakao.maps.Marker({
          position: position, //  
          image: markerImage,
        });

      marker.setMap(map); //    
      markers.push(marker); //    

      return marker;
    }

    //       
    function removeMarker() {
      for (let i = 0; i < markers.length; i++) {
        markers[i].setMap(null);
      }
      markers = [];
    }

    //         
    function displayPlaceInfo(place: any) {
      let content =
        '<div class="placeinfo">' +
        '   <a class="title" href="' +
        place.place_url +
        '" target="_blank" title="' +
        place.place_name +
        '">' +
        place.place_name +
        "</a>";

      if (place.road_address_name) {
        content +=
          '    <span title="' +
          place.road_address_name +
          '">' +
          place.road_address_name +
          "</span>" +
          '  <span class="jibun" title="' +
          place.address_name +
          '">( : ' +
          place.address_name +
          ")</span>";
      } else {
        content +=
          '    <span title="' +
          place.address_name +
          '">' +
          place.address_name +
          "</span>";
      }

      content +=
        '    <span class="tel">' +
        place.phone +
        "</span>" +
        "</div>" +
        '<div class="after"></div>';

      contentNode.innerHTML = content;
      placeOverlay.setPosition(new kakao.maps.LatLng(place.y, place.x));
      placeOverlay.setMap(map);
    }

    //     
    function addCategoryClickEvent() {
      const category = document.getElementById("category"),
        children = category?.children;
      // @ts-ignore
      for (let i = 0; i < children.length; i++) {
        // @ts-ignore
        children[i].onclick = onClickCategory;
      }
    }

    //     
    // eslint-disable-next-line no-unused-vars
    function onClickCategory(this: any) {
      var id = this.id,
        className = this.className;

      placeOverlay.setMap(null);

      if (className === "on") {
        currCategory = "";
        // @ts-ignore
        changeCategoryClass();
        removeMarker();
      } else {
        currCategory = id;
        changeCategoryClass(this);
        searchPlaces();
      }
    }

    //      
    function changeCategoryClass(el: any) {
      var category = document.getElementById("category"),
        children = category?.children,
        i;
      // @ts-ignore
      for (i = 0; i < children?.length; i++) {
        // @ts-ignore
        children[i].className = "";
      }
      if (el) {
        el.className = "on";
      }
    }
  }, [latitude]);

  return (
    <div className="map_wrap">
      <div
        id="map"
        style={{
          width: "100%",
          height: "100%",
          position: "relative",
          overflow: "hidden",
        }}
      />
      <ul id="category">
        <li id="BK9" data-order="0">
          <span className="category_bg bank" />
          
        </li>
        <li id="MT1" data-order="1">
          <span className="category_bg mart" />
          
        </li>
        <li id="PM9" data-order="2">
          <span className="category_bg pharmacy" />
          
        </li>
        <li id="OL7" data-order="3">
          <span className="category_bg oil" />
          
        </li>
        <li id="CE7" data-order="4">
          <span className="category_bg cafe" />
          
        </li>
        <li id="CS2" data-order="5">
          <span className="category_bg store" />
          
        </li>
      </ul>
    </div>
  );
};

export default Map;
